import React, { useState, useEffect } from 'react'
import { Link, useSearchParams, useNavigate, redirect } from 'react-router-dom'
import { Form, Button, Row, Col } from 'react-bootstrap'
import { useDispatch, useSelector  } from 'react-redux'
import Loader from '../components/Loader'
import Message from '../components/Message'
import { login } from '../actions/userActions'
import FormContainer from '../components/FormContainer'

function LoginScreen() {

    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    let navigate = useNavigate()

    const dispatch = useDispatch()

    const [searchParams, setSearchParams] = useSearchParams()

    const redir = searchParams.get('redirect') ? searchParams.get('redirect') : '/'

    const userLogin = useSelector(state => state.userLogin)
    const { error, loading, userInfo } = userLogin

    useEffect(() => {
        if (userInfo){
            navigate(redir)
        }
    }, [userInfo, redir])
    

    const submitHandler = (e)=> {

        e.preventDefault()
        console.log('Submitted')
        dispatch(login(email,password))
    }


  return (
    <FormContainer>
      <h1 className='py-3 text-center'>Sign In</h1>
      {error && <Message variant='danger'>{error}</Message>}
      {loading && <Loader />}

    
      <Form onSubmit={submitHandler}>
            <Form.Group controlId='email'>
                <Form.Label>Email Address</Form.Label>
                <Form.Control type='email' placeholder='Enter Email' value={email} onChange={(e) => setEmail(e.target.value)} ></Form.Control>
            </Form.Group>


            <Form.Group controlId='password'>
                <Form.Label>Password</Form.Label>
                <Form.Control type='password' placeholder='Enter Password' value={password} onChange={(e) => setPassword(e.target.value)} ></Form.Control>
            </Form.Group>

            <Button type='submit' variant='primary' className='my-3 btn-success' >
                Sign In
            </Button>
      </Form>

      <Row className='py-3'>

        <Col>
            New Customer? <Link to={redirect ? `/register?redirect=${redir}` : '/register'}>Register</Link>
        </Col>
        
      </Row>
    </FormContainer>
  )
}

export default LoginScreen
